import api from '../http'
import qs from 'qs'
export const getTeamListForPage = (queryForm) => {
	return api.http({
		url:'/shop/team/list/page',
		method:'get',
		params:queryForm
	})
}

export const getMyTeamListForPage = (queryForm) => {
	return api.http({
		url:'/shop/team/list/page/me',
		method:'get',
		params:queryForm
	})
}

export const teamInsert = (data) => {
	return api.http({
		url:'/shop/team/insert/team',
		method:'post',
		data:qs.stringify(data),
		headers:{
			"Content-Type":"application/x-www-form-urlencoded"
		}
	})
}

export const getResult = (params) => {
	return api.http({
		url:'/shop/team/get/result',
		method:'get',
		params
	})
}

